.friendMessage {
    display: flex;
    flex-direction: column;
    font-family: Montserrat, sans-serif;
    position: relative;
    margin:48px 0 ;
}

.friendImageAndText {
display: flex;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
}

.friendText {
    padding: 7px 25px 8px 13px;
    border-radius:10px ;
}

.friendText:after {

}

.friendName {
    color: #000;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 3px;
}

.friendMessageText {
    color: #000;
    font-size: 16px;
}

.friendTime {
    color: #000;
    font-size: 10px;
    font-weight: 600;
    position: absolute;
    left: 10px;
    bottom: -10px
}
