.message {
    display: flex;
    flex-direction: column;
    font-family: Montserrat, sans-serif;
    position: relative;
    margin:48px 0 ;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 48px;
    height: 48px;
}

.text {
    background-color: #06C;
    padding: 7px 25px 8px 13px;
    border-radius:10px ;
}

.text:after {
}

.name {
    text-align: right;
    color: #FFF;
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 3px;
}

.messageText {
    color: #FFF;
    font-size: 16px;
}

.time {
    color: #000;
    font-size: 10px;
    font-weight: 600;
    position:absolute;
    right: 10px;
    bottom: -10px;
}